import multipleInstances from "./multipleInstances";
import parseQuerystring from "../modules/ParseQuerystring";

/**
 * Create paged URL parameters after being selected in the paging add-on.
 * Triggered from Paging add-on.
 *
 * @param {object}  alm  Core ALM object.
 * @param {Boolean} init Is this the first run.
 * @since 1.1
 */
export function createPagingURL(alm, init) {
	const nested = alm.main.dataset.nested === "true" ? true : false; // Nested ALM instance

	// Exit if finished, nested or multiple instances.
	if (alm.finished || nested || multipleInstances()) {
		return false;
	}
	let obj = {};
	let page = alm.page + 1;
	page = alm.preloaded ? page + 1 : page; // Add 1 for preloaded.

	const querystring = window.location.search.substring(1);
	if (querystring) {
		obj = parseQuerystring(querystring);
		if (Object.keys(obj).length) {
			obj.pg = page;
		}
	} else {
		obj.pg = page; // Empty querystring
	}

	let url = "?";
	let count = 0;

	// Loop the object and build the querystring
	Object.keys(obj).forEach(function (key) {
		count++;
		if (count > 1) {
			url += "&";
		}
		url += `${key}=${obj[key]}`;
	});

	// Confirm URL
	url = page == 1 ? url.replace(/\A?pg=[^&]+&*/g, "") : url; // Regex to check for ?pg=1 parmaeters
	url =
		url[url.length - 1] === "?" || url[url.length - 1] === "&"
			? url.substring(0, url.length - 1)
			: url; // Remove orphan ? || & querystring params
	url = url === "" ? alm.canonical_url : url; // If empty, set to alm.canonical_url

	// Set the URL - use replaceState to prevent bck/fwd interactions
	if (alm.addons.filters_paging) {
		const state = { permalink: url };
		history.replaceState(state, null, url);
	}

	// Scroll User to top of listing
	const scroll = alm.addons.filters_scroll;
	if (typeof ajaxloadmore.almScroll === "function" && scroll && !init) {
		let target = alm.listing;
		if (!target) {
			return false;
		}
		const offset = ajaxloadmore.getOffset(target).top;
		const scrolltop = alm.addons.filters_scrolltop
			? parseInt(alm.addons.filters_scrolltop)
			: 30;
		const top = offset - scrolltop + 1;
		ajaxloadmore.almScroll(top);
	}
}
