import vars from '../global/Variables';

/**
 * Get all filter instances.
 *
 * @return {NodeList} The filter instances.
 */
export function getFilterInstances() {
	return document.querySelectorAll(vars.filters_classname);
}

/**
 * Get all individual filters.
 *
 * @return {NodeList} The filter instances.
 */
export function getFilters() {
	return document.querySelectorAll(`${vars.filters_classname} ${vars.filter_classname}`);
}

/**
 * Get all filters by ID (target).
 * @param {string} id The filter ID.
 * @return {Array}    An array of filters.
 */
export function getFiltersByTarget(id) {
	// Get all filter instances on the page.
	const filters = getFilterInstances();
	if (!filters) {
		return [];
	}

	const filterArray = [];

	// Loop all filter instances.
	filters.forEach((item) => {
		let target = item.dataset.target;
		if (target) {
			target = target.split(',');
			if (target.includes(id)) {
				// Get all filters inside the filter instance.
				const elements = item.querySelectorAll(vars.filter_classname);
				// Add filters to filterArray.
				filterArray.push(...elements);
			}
		}
	});

	return filterArray;
}
