import vars from '../global/Variables';
import { getFilters } from '../helpers/getInstances';
import { setHighlightedStars } from '../types/StarRating';
import clearInputs from './ClearInputs';
import setCheckboxState from './SetCheckboxState';
import triggerChange from './TriggerChange';

/**
 * Get the selected terms and build the data obj.
 *
 * @param {object} filter Object containing selected filter properties.
 * @since 1.0
 */
const setElementStates = (urlArray) => {
	const almFilters = document.querySelector(vars.filters_classname);
	if (!almFilters) {
		return; // Exit if Filters instance does not exist.
	}

	vars.alm_filtering_popstate = true; // Set popstate trigger.
	const filters = getFilters(); // Get all filters on the page.

	// Loop all filters
	[...filters].forEach((filter) => {
		const fieldtype = filter.dataset.fieldtype;
		let key = filter.dataset.key;
		key = key === 'taxonomy' ? filter.dataset.taxonomy : key; // If key is taxonomy, convert key to taxonomy slug
		key = key === 'meta' ? filter.dataset.metaKey : key; // If key is meta, convert key to meta key

		switch (fieldtype) {
			case 'checkbox': // Checkbox
				const checkboxes = filter.querySelectorAll('div.field-checkbox'); // All checkboxes
				// If key matches URL key
				if (urlArray.hasOwnProperty(key)) {
					const valueArray = urlArray[key].split('+');
					[...checkboxes].forEach((checkbox) => {
						setCheckboxState(valueArray, checkbox);
					});
				} else {
					clearInputs(filter, checkboxes); // Clear all
				}

				break;

			case 'radio': // Radios
				const radios = filter.querySelectorAll('div.field-radio'); // All radios
				if (urlArray.hasOwnProperty(key)) {
					const valueArray = urlArray[key].split('+');
					[...radios].forEach((radio) => {
						setCheckboxState(valueArray, radio);
					});
				} else {
					clearInputs(filter, radios); // Clear all
				}
				break;

			case 'star_rating': // Star Rating... Duplicate of Radio func
				const stars = filter.querySelectorAll('div.field-starrating'); // All radios

				if (urlArray.hasOwnProperty(key)) {
					const valueArray = urlArray[key].split('+');
					[...stars].forEach((star) => {
						setCheckboxState(valueArray, star);
					});
					setHighlightedStars(filter, stars);
				} else {
					clearInputs(filter, stars); // Clear all
				}
				break;

			case 'select': // Select
				const select = filter.querySelector('select');
				if (urlArray.hasOwnProperty(key)) {
					select.value = urlArray[key];
				} else {
					select.value = '#';
				}
				break;

			case 'select_multiple': // Select
				const select_multiple = filter.querySelector('select');
				if (urlArray.hasOwnProperty(key)) {
					select_multiple.value = urlArray[key];
				} else {
					select_multiple.value = '#';
				}
				break;

			case 'date_picker': // Select
				const datepicker = filter.querySelector('.flatpickr-input');
				if (urlArray.hasOwnProperty(key)) {
					// Replace `+` with ` | ` for range mode
					const value = urlArray[key].replace('+', ' | ');
					datepicker.value = value;
				} else {
					datepicker.value = '';
				}
				break;

			default: // Textfield
				const textfield = filter.querySelector('input[type=text]');
				if (urlArray.hasOwnProperty(key)) {
					textfield.value = urlArray[key];
				} else {
					textfield.value = '';
				}
				break;
		}
	});

	triggerChange(almFilters);
};
export default setElementStates;
