import vars from '../global/Variables';

/**
 * Count ALM core instances.
 *
 * @return {boolean} Are there multiple instances of core ALM on the page.
 * @since 1.8.1
 */
export default function multipleInstances() {
	const instances = document.querySelectorAll(vars.alm_core_classname);
	return instances && instances.length > 1 ? true : false;
}
