import sortString from '../helpers/sortAlphaStr';

/**
 * Build the custom filter URL.
 *
 * @param  {HTMLElement} filter     The target filter container.
 * @param  {string} 		 currentURL The current URL.
 * @return {string}                 The modified URL.
 * @since 1.0
 */

const buildURL = (filter, currentURL) => {
	const key = filter.dataset.key;
	const fieldtype = filter.dataset.fieldtype;
	const taxonomy = filter.dataset.taxonomy;
	const metaKey = filter.dataset.metaKey;

	let url = '';
	let title = key === 'taxonomy' ? `${taxonomy}` : `${key}`; // Convert type to taxonomy slug
	title = key === 'meta' ? `${metaKey}` : title; // Convert type to custom field slug

	// If current URL is empty, prepend ? for the querystring
	title = currentURL === '' ? `?${title}` : `&${title}`;

	// Get preselected value
	let preselected = filter.dataset.selectedValue;

	switch (fieldtype) {
		case 'select_multiple':
			let mSelect = filter.querySelector('select');
			let options = mSelect && mSelect.options;
			let mSelectVal = '';
			let mSelectCount = 0;

			if (!mSelect) {
				break; // exit if empty
			}

			// Loop all <option/> elements to build URL
			[...options].forEach((option) => {
				let value = option.value;
				if (option.selected) {
					mSelectVal += mSelectCount > 0 ? '+' : '';

					if (value !== '') {
						// Confirm option has a value
						mSelectVal += value;
						mSelectCount++;
					}
				}
			});
			url += mSelectCount > 0 ? `${title}=${mSelectVal}` : url;

			break;

		case 'select':
			let select = filter.querySelector('select');
			if (!select) {
				break; // exit if empty
			}

			let value = select.value;

			// Remove meta_value params from URL
			//value = "sort" === key ? removeMetaValue(value) : value;

			url += value === '#' || value === preselected ? '' : `${title}=${value}`;

			break;

		case 'text':
		case 'range_slider':
			let textfield = filter.querySelector('input[type=text]');
			if (!textfield) {
				break; // exit if empty
			}

			const inputValue = textfield.value;

			// If inputValue is empty or whitespace, return empty string.
			url += inputValue === '' || inputValue.trim().length === 0 ? '' : `${title}=${inputValue}`;

			break;

		case 'date_picker':
			let datepicker = filter.querySelector('input.flatpickr-input');
			if (!datepicker) {
				break; // exit if empty
			}

			if (datepicker.value) {
				// Replace ` | ` with `+` for range mode
				let value = datepicker.value.replace(' | ', '+');
				url += datepicker.value === '' ? '' : `${title}=${value}`;
			}

			break;

		default:
			let items = filter.querySelectorAll('.alm-filter--link'); // Get all inputs
			let checkedVal = '';
			let count = 0;

			if (!items.length) {
				break; // exit if empty
			}

			[...items].forEach((item) => {
				let value = item.dataset.value;
				if (item.classList.contains('active')) {
					if (count > 0 && value !== '') {
						checkedVal += '+';
					}

					if (value !== '') {
						// Confirm checked has a value
						checkedVal += value;
						count++;
					}
				}
			});

			// If has selection and doesn't equal the preselected value
			// Remove the preselected value as it shouldn't be shown in the URL
			url += count > 0 && sortString(preselected) !== sortString(checkedVal) ? `${title}=${checkedVal}` : url;
	}

	return url;
};

export default buildURL;
