/**
 * Set the default state of the filter.
 *
 * @param {Element} container The filter container.
 * @return null
 * @since 1.2
 */
export function setDefaults(container) {
	let found = false;
	const items = container.querySelectorAll('.alm-filter--link');
	[...items].forEach((item) => {
		if (item.classList.contains('active')) {
			found = true;
		}
	});

	if (found) {
		return false;
	}

	const default_item = container.querySelector('.alm-filter--link[data-selected="true"]');
	if (default_item) {
		default_item.classList.add('active');
		default_item.setAttribute('aria-checked', true);
	}
}

/**
 * Restore the default value of the filter.
 *
 * @param {Element} filter The filter container.
 * @since 1.2
 */
export function restoreDefault(filter) {
	const fieldtype = filter.dataset.fieldtype; // Filter container
	let selected_value = filter.dataset.selectedValue; // Default selected value

	switch (fieldtype) {
		case 'radio':
		case 'checkbox':
			let items = filter.querySelectorAll('.alm-filter--link');
			[...items].forEach((item) => {
				if (item.dataset.value === selected_value && item.dataset.type !== 'all') {
					// If item is preselected.
					item.classList.add('active');
					item.setAttribute('aria-checked', true);
				} else {
					item.classList.remove('active');
					item.setAttribute('aria-checked', false);
				}
			});

			break;

		case 'select':
			let select = filter.querySelector('select');
			if (!select) {
				break; // exit if empty
			}

			// If (pre)selected value is null, set value to #
			selected_value = selected_value == null || selected_value === '' ? '#' : selected_value;

			[...select.options].forEach((item) => {
				if (item.value === selected_value) {
					select.value = item.value; // Set <select/> value
				}
			});

			break;

		case 'select_multiple':
			let select_multiple = filter.querySelector('select');
			if (!select_multiple) {
				break; // exit if empty
			}

			// If (pre)selected value is null, set value to #
			selected_value = selected_value == null || selected_value === '' ? '#' : selected_value;

			// Loop all options and deselect.
			[...select_multiple.options].forEach((item) => {
				item.selected = false;
			});

			break;

		default:
			// textfield

			let input = filter.querySelector('input');
			if (!input) {
				break; // exit if empty
			}

			input.value = '';

			break;
	}
}

/**
 * Get the default/fallback value of the filter

 * @return returnVal string
 * @since 1.4.1
 */
export function getDefault(filter) {
	let returnVal = '';
	let key = filter.dataset.key;
	let defaultValue = filter.dataset.defaultValue;

	if (defaultValue) {
		// If defaultValue is set
		returnVal = defaultValue;
	}

	return returnVal;
}
