import * as a11yarrows from 'a11yarrows';
import { setCheckboxLimits, uiToggle } from './global/UI';
import vars from './global/Variables';
import { createPagingURL } from './helpers/addons';
import { getFilterInstances, getFilters } from './helpers/getInstances';
import multipleInstances from './helpers/multipleInstances';
import { toggleAll, toggleSelect } from './helpers/toggle';
import { missingALMInstance, missingTarget } from './helpers/warnings';
import buildDataObj from './modules/BuildDataObj';
import setCurrentFilters from './modules/CurrentFilters';
import { restoreDefault } from './modules/Defaults';
import dispatch from './modules/Dispatch';
import setFacets from './modules/Facets';
import parseQuerystring from './modules/ParseQuerystring';
import onScroll from './modules/Scroll';
import setElementStates from './modules/SetSelectedElements';
import triggerChange from './modules/TriggerChange';
import getKeyElement from './modules/currentFilters/getKeyElement';
import getKeyObject from './modules/currentFilters/getKeyObject';
import { setDatePickers } from './types/DatePicker';
import { setRangeSliders } from './types/RangeSliders';
import starRating, { clearHighlightedStars } from './types/StarRating';
require('./helpers/polyfills');

/**
 * Initiate the filter objects.
 *
 * @param {HTMLELement} filter The container element for the filter.
 * @since 1.0
 */
const almFiltersInit = (filter) => {
	if (!filter) {
		return;
	}

	const { style, target = '' } = filter.dataset;
	if (!target) {
		missingTarget(); // Display console warning if target is missing.
	}

	// Loop each instance.
	target.split(',').forEach((target) => {
		const instance = document.querySelector(`.ajax-load-more-wrap[data-id="${target}"]`);
		if (!instance) {
			missingALMInstance(target); // Display console warning if ALM instance is missing.
		}
	});

	// Click/Change Event
	const almFiltersClick = () => {
		if (vars.alm_filtering) {
			return false; // exit if animating/loading
		}
		triggerChange(filter);
	};

	// Radio + Checkbox Click Event
	const almFilterChange = (event) => {
		event.preventDefault();

		if (vars.alm_filtering) {
			return false; // Exit if animating/loading
		}

		const element = event.currentTarget;
		const fieldtype = element.dataset.type;
		const current_id = element.id;
		const parent = element.closest('.alm-filter'); // <div .alm-filter />// All items in filter
		const items = parent.querySelectorAll('.alm-filter--link'); // All items in filter

		if (fieldtype === 'all') {
			if (element.classList.contains('active')) {
				// Uncheck All
				[...items].forEach((item, e) => {
					item.classList.remove('active');
					item.setAttribute('aria-checked', false);
				});
			} else {
				// Check All
				[...items].forEach((item, e) => {
					item.classList.add('active');
					item.setAttribute('aria-checked', true);
				});
			}
		} else {
			if (fieldtype === 'radio' || fieldtype === 'star_rating') {
				// Exit if active and preselected value set
				if (parent.classList.contains('alm-filter--preselected') && element.classList.contains('active')) {
					event.preventDefault();
					return false;
				}

				// Loop all radios
				[...items].forEach((item, e) => {
					if (item.id !== current_id) {
						item.classList.remove('active');
						item.setAttribute('aria-checked', false);
					}
				});
			}

			// Set active state
			if (element.classList.contains('active')) {
				element.classList.remove('active');
				element.setAttribute('aria-checked', false);
			} else {
				element.classList.add('active');
				element.setAttribute('aria-checked', true);
			}

			// Check for `toggle All` button
			toggleAll(fieldtype, items, parent);
		}

		// Trigger Change Event
		if (style === 'change') {
			triggerChange(filter);
		}
	};

	// Radio + Checkbox Event listeners
	const almFilterLinks = filter.querySelectorAll('.alm-filter--link');
	if (almFilterLinks) {
		[...almFilterLinks].forEach((item, e) => {
			item.addEventListener('click', almFilterChange);
			item.addEventListener('keyup', function (event) {
				const { keyCode } = event;
				if (keyCode === 13 || keyCode === 32) {
					// Enter/return click || spacebar
					almFilterChange(event);
				}
			});
			item.addEventListener('keydown', function (event) {
				const { keyCode } = event;
				if (keyCode === 32) {
					//  Prevent the default scrollbar action of scrolling the page
					event.preventDefault();
					event.stopPropagation();
					return false;
				}
			});
		});
	}

	// Radio + Checkbox - a11yarrows controls
	const radioInputs = filter.querySelectorAll('div.alm-filter[data-fieldtype=radio]');
	if (radioInputs) {
		[...radioInputs].forEach((item) => {
			const target = item.querySelector('ul');
			a11yarrows.init(target, {
				selector: '.alm-filter--link', // options
			});
		});
	}

	// Star Rating
	const starRatings = filter.querySelectorAll('div.alm-filter[data-fieldtype=star_rating]');
	if (starRatings) {
		[...starRatings].forEach((rating) => {
			let stars = rating.querySelectorAll('li div.field-starrating');
			if (stars) {
				starRating(stars);
			}
		});
	}

	// Textfield Button Event listeners.
	const almFiltertextButtons = filter.querySelectorAll('.alm-filter--text-wrap.has-button button');
	if (almFiltertextButtons) {
		[...almFiltertextButtons].forEach((button) => {
			button.addEventListener('click', almFiltersClick);
		});
	}

	// Change Event (Select).
	if (style === 'change') {
		// Loop all items and add the event listener
		let almFilterItems = filter.querySelectorAll('.alm-filter--item');
		if (almFilterItems) {
			[...almFilterItems].forEach((item) => {
				item.addEventListener('change', almFiltersClick);
			});
		}
	}

	// Button.
	if (style === 'button') {
		let almFilterButton = filter.querySelector('.alm-filters--button');
		if (almFilterButton) {
			almFilterButton.addEventListener('click', almFiltersClick);
		}
	}

	// Reset Button.
	const resetButton = filter.querySelector(vars.reset_btn_classname);
	if (resetButton) {
		resetButton.addEventListener('click', function () {
			window.almFiltersClear(true, filter);
		});
	}

	// Event listeners for input textfields.
	const almFilterTextfields = filter.querySelectorAll('.alm-filter--textfield');
	if (almFilterTextfields) {
		[...almFilterTextfields].forEach((item) => {
			item.addEventListener('keyup', function (event) {
				const { keyCode } = event;
				if (keyCode === 13) {
					almFiltersClick(); // Enter/return click
				}
			});
			// Focus + Focusout for Textfields.
			item.addEventListener('focus', function (event) {
				const parent = event.target.parentNode;
				parent.classList.add('has-focus');
			});
			item.addEventListener('focusout', function (event) {
				const parent = event.target.parentNode;
				parent.classList.remove('has-focus');
			});
		});
	}

	// Datepicker init.
	const datePickers = filter.querySelectorAll('input.alm-flatpickr');
	if (datePickers) {
		setDatePickers(filter.dataset.id, datePickers);
	}

	// RangeSlider init.
	const rangeSliders = filter.querySelectorAll('div.alm-range-slider');
	if (rangeSliders) {
		setRangeSliders(filter.dataset.id, rangeSliders, style);
	}

	// Toggle Filter button.
	const filterToggles = filter.querySelectorAll('div.alm-filter--title .alm-filter--toggle');
	if (filterToggles) {
		uiToggle(filterToggles);
	}

	// Checkbox Limit buttons.
	const checkboxLimitToggles = filter.querySelectorAll('a.alm-filter--checkbox-limit');
	if (checkboxLimitToggles) {
		setCheckboxLimits(checkboxLimitToggles);
	}

	// Set currently selected filters.
	setCurrentFilters(window.location.search);
};

/**
 * Facets callback dispatched from core ALM.
 *
 * @param {object} facets The current facets.
 */
window.almFiltersFacets = function (facets) {
	setFacets(facets);
};

/**
 * Reset Button Callback.
 *
 * @param {object} obj The active filters object.
 */
window.almFiltersResetStatus = function (obj) {
	const resetButtons = document.querySelectorAll(vars.reset_btn_classname);
	if (!resetButtons) {
		return;
	}

	[...resetButtons].forEach((button) => {
		if (obj === '') {
			button.style.display = 'none';
		} else {
			button.style.display = 'block';
		}
	});
};

/**
 * Trigger click event on selected filter.
 *
 * @param {Element} element The clicked element.
 */
window.removeSelectedFilter = (element) => {
	const { key, value } = element.dataset;
	const obj = getKeyObject(key);

	const { target, fieldType } = obj;
	if (!target || !fieldType) {
		return;
	}
	const el = getKeyElement(target, value, fieldType);

	// Get the parent filter group.
	const filter = target.closest(vars.filters_classname);
	if (!filter) {
		return;
	}

	switch (obj.fieldType) {
		case 'select':
			// if has a selected value
			el.value = target.dataset.selectedValue ? target.dataset.selectedValue : '#';
			triggerChange(filter);
			break;

		case 'text':
			el.value = '';
			triggerChange(filter);
			break;

		default:
			el.click();
			if (filter.dataset.style === 'button') {
				triggerChange(filter);
			}
			break;
	}
};

/**
 * Trigger click event on selected filter when enter clicked.
 *
 * @param {Element} element The clicked element
 */
window.removeSelectedFilterEnter = (event) => {
	const { keyCode } = event;
	if (keyCode === 13) {
		removeSelectedFilter(event.target); // Enter/Return click
	}
};

/**
 * Initiate Ajax Load More filters.
 * Public JS function.
 *
 * @param {string}  filter_id     The filter instance ID.
 * @param {boolean} isBlockEditor Is this a block editor instance.
 * @since 1.7.5
 */
const start = function (filter_id = null, isBlockEditor = false) {
	const { filters_classname: className } = vars;
	const almFilters = filter_id ? document.querySelectorAll(`${className}[data-id="${filter_id}"]`) : document.querySelectorAll(`${className}`);

	if (!almFilters) {
		return; // Exit if empty.
	}

	// Loop each filter.
	almFilters.forEach((filter) => {
		// Initiate each filter instance.
		almFiltersInit(filter);

		// Set the toggle state of the filter.
		toggleSelect(filter);

		if (isBlockEditor) {
			return true; // Exit early if in the WP Block Editor.
		}

		const target = filter.dataset.target;
		const alm = document.querySelector(`.ajax-load-more-wrap[data-id="${target}"]`);
		if (alm) {
			const almListing = alm.querySelector('.alm-listing[data-filters=true]');
			// Set scroll & touch listeners if required to update the paging URL.
			// Exit if Paging add-on is active so we don't update the browser URL.
			if (!multipleInstances() && almListing && almListing.dataset.paging !== 'true') {
				const pagination = almListing.dataset.filtersPaging;
				if (pagination === 'true') {
					window.addEventListener('touchstart', function () {
						onScroll(filter, almListing);
					});
					window.addEventListener('scroll', function () {
						onScroll(filter, almListing);
					});
				}
			}
		}
	});
};
export { start };

/**
 * Initiate Filters on page load.
 */
window.addEventListener('load', function () {
	if (vars.almFilters) {
		start();
	}

	// Dispatch history change event on page load to allow for pushstate to trigger on first page.
	if (typeof window.history.pushState === 'function') {
		const url = window.location.search;
		const state = { permalink: url };
		history.replaceState(state, null, url);
	}
});

/**
 * Fires when users click back or forward browser buttons.
 * Popstate is only available when a single instance of Ajax Load More is on the screen.
 */
window.addEventListener('popstate', function (event) {
	// Safari popstate fix
	// Safari triggers a popstate anytime the back button is pressed.
	// This flag prevents execution from articles or other pages.
	const almFilters = vars.almFilters || document.querySelector(vars.filters_classname);

	// Exit if element does not exist.
	// Exit if pushstate was never initiated.
	// Exit if multiple instances.
	if (!almFilters || !vars.pushstate || multipleInstances()) {
		return;
	}

	// Fix for Hash anchor causing popstate.
	if (event.state === null) {
		event.preventDefault();
		return;
	}

	const querystring = window.location.search;
	let url = event.state ? event.state.permalink : querystring; // Get state or querystring
	url = url.replace('?', ''); // remove `?` param

	const resetButton = document.querySelector(vars.reset_btn_classname);

	// Empty URL or empty querystring.
	if (!url || querystring === '') {
		window.almFiltersClear(false);
		if (resetButton) {
			resetButton.style.display = 'none';
		}
	} else {
		setElementStates(parseQuerystring(url));
		if (resetButton) {
			resetButton.style.display = 'block';
		}
	}
});

/**
 * Create paged URL parameters.
 * Triggered from Paging add-on.
 *
 * @param {object}  alm  Core ALM object.
 * @param {Boolean} init Is this the first run.
 * @since 1.0
 */
window.almFiltersPaged = (alm, init = true) => {
	createPagingURL(alm, init);
};

/**
 * Reset all filters back to default state.
 * Public JS function.
 *
 * @param {boolean} reset  Should this reset the entire filter.
 * @param {Element} filter An optional filter element to pass.
 * @since 1.7.5
 */
const reset = function (reset = true, filter = null) {
	const almFilters = filter ? filter : vars.almFilters || document.querySelector('.alm-filters-container');

	if (!almFilters) {
		return false;
	}

	const target = almFilters.dataset.target; // Get filter target.
	const filters = getFilters(); // Get all filter instances.
	if (!filters) {
		return;
	}

	let data = {};

	// Loop all filters
	[...filters].forEach((filter) => {
		restoreDefault(filter);
		data = buildDataObj(filter, data);
	});

	if (reset) {
		// Trigger change event.
		triggerChange(almFilters);
	} else {
		// Dispatch filter change
		dispatch(target, data);
	}
};
export { reset };

/**
 * Legacy public function.
 *
 * @since 1.0
 */
window.almFiltersClear = function (reset = true, filter = null) {
	clearHighlightedStars();
	almfilters.reset(reset, filter);
};

/**
 * Get all active filters.
 * Public JS function.
 *
 * @return {Object} filter The HTML element to reset.
 * @since 2.2.1
 */
const getActiveFilters = function () {
	const querystring = window.location.search.replace('?', '');
	if (!querystring) {
		return {};
	}

	const params = parseQuerystring(querystring); // Get current querystring.
	const filters = {};

	// Loop each querystring to build an object.
	for (const property in params) {
		filters[property] = params[property];
	}

	return filters;
};
export { getActiveFilters };

/**
 * Reset an individual filter group `almfilters.restoreDefault()`
 * Public JS function.
 *
 * @param {Element} filter The HTML element to reset.
 * @since 1.7.5
 */
const resetFilter = function (filter) {
	if (filter) {
		restoreDefault(filter);
	}
};
export { resetFilter };

/**
 * Load a Filters instance inside the WP Block Editor.
 *
 * @since 7.1.0
 * @param {Element} instance The HTML element.
 */
export const wpblock = function (instance) {
	const filter = instance.querySelector('.alm-filters-container');
	if (!filter || filter.dataset.blockLoaded === 'true') {
		return true; // Exit if does not exist or block already loaded.
	}
	filter.dataset.blockLoaded = 'true';
	const id = filter.dataset.id;
	if (id) {
		start(id, true);
	}
};

/**
 * Scroll user to page on page load.
 * Fires from core Ajax Load More [core/src/js/ajax-load-more.js]
 *
 * @param {object} alm The global ALM object.
 * @since 1.7
 */
window.almFiltersOnload = function (alm = null) {
	if (!alm || multipleInstances()) {
		return false;
	}

	// Delay to give posts time to load
	setTimeout(function () {
		const page = parseInt(alm.page) + 1;
		if (page > 1) {
			let target = alm.listing.querySelector('.alm-filters[data-page="' + page + '"]');
			if (target) {
				let offset = typeof ajaxloadmore.getOffset === 'function' ? ajaxloadmore.getOffset(target).top : target.offsetTop;
				let scrolltop = alm.addons.filters_scrolltop ? parseInt(alm.addons.filters_scrolltop) : 30;
				let top = offset - scrolltop + 1;
				window.scrollTo(0, top);
			}
		}
	}, vars.delay);
};

/**
 * Filters Complete function.
 * Fires from core Ajax Load More [core/src/js/modules/filtering.js]
 *
 * @param {Element} element The alm element.
 * @since 1.0
 */
window.almFiltersAddonComplete = function (element = null) {
	const target = element.querySelector('.alm-listing');
	let scroll = false;
	let scrolltop = 30;

	if (target) {
		scroll = target.dataset.filtersScroll === 'true' ? true : false;
		scrolltop = target.dataset.filtersScrolltop ? parseInt(target.dataset.filtersScrolltop) : 30;
	}

	// Add slight delay for re-initialization of filters.
	setTimeout(function () {
		vars.alm_filtering = false;

		// Get all filter instances.
		const filterInstances = getFilterInstances();
		[...filterInstances].forEach((instance) => {
			instance.classList.remove('filtering'); // Remove loading state on each filter instance.
		});

		/*
		 * Callback function dispatched after the filters have completed.
		 */
		if (typeof window.almFiltersComplete === 'function') {
			window.almFiltersComplete();
		}

		// Scroll User to top of listing.
		if (typeof ajaxloadmore.almScroll === 'function' && scroll) {
			if (!element) {
				return;
			}

			const offset = typeof ajaxloadmore.getOffset === 'function' ? ajaxloadmore.getOffset(element).top : element.offsetTop;
			const top = offset - scrolltop + 1;
			ajaxloadmore.almScroll(top);
		}
	}, vars.delay);
};
