import vars from '../global/Variables';
import { getFilterInstances, getFilters, getFiltersByTarget } from '../helpers/getInstances';
import multipleInstances from '../helpers/multipleInstances';
import parseQueryString from '../helpers/parseQueryString';
import { missingTarget } from '../helpers/warnings';
import buildDataObj from './BuildDataObj';
import buildURL from './BuildURL';
import dispatch from './Dispatch';

/**
 * Get the selected terms and append them to data obj.
 *
 * @param {HTMLElement} filterGroup The filters element container.
 * @since 1.0
 */
const triggerChange = (filterGroup) => {
	/*
	 * Callback functionis dispatched when a filter change event is triggered.
	 */
	if (typeof window.almFiltersChange === 'function') {
		window.almFiltersChange();
	}

	// Get all filter instances.
	const filterInstances = getFilterInstances();
	[...filterInstances].forEach((instance) => {
		instance.classList.add('filtering'); // Set loading state on each filter instance.
	});

	vars.alm_filtering = true;

	// Get filter params from data attributes.
	const { target: targets, redirect = false } = filterGroup.dataset;

	if (!targets) {
		missingTarget(); // Display console warning if target is missing.
	}

	// Loop each ALM target.
	targets.split(',').forEach((id) => {
		// Get the target .ajax-load-more element
		const alm = document.querySelector('.ajax-load-more-wrap[data-id="' + id + '"]') || false;
		if (!alm) {
			return;
		}

		const { canonicalUrl } = alm.dataset; // Get the canonical URL
		const hasMultple = multipleInstances(); // Are there multiple instances of core ALM on the page.

		let data = {};
		let url = '';

		// Get all matching filters on the page.
		const filters = getFiltersByTarget(id);

		// Loop all filters.
		[...filters].forEach((filter) => {
			data = buildDataObj(filter, data); // Build data object to send to ALM.
			if (!hasMultple) {
				url += buildURL(filter, url); // Build the URL.
			}
		});

		// Redirect user to new URL after filter.
		if (redirect) {
			window.location.href = `${redirect}${url}`;
			return;
		}

		// Build an object of active filters.
		const activeFilters = url !== '' ? parseQueryString(url, true) : '';

		/*
		 * Callback function dispatched informing user of the active filters.
		 */
		if (typeof window.almFiltersActive === 'function') {
			window.almFiltersActive(activeFilters);
		}

		// Set the reset button status.
		if (typeof window.almFiltersResetStatus === 'function') {
			window.almFiltersResetStatus(activeFilters);
		}

		// Set new URL.
		url = url === '' ? canonicalUrl : url;

		// If pushstate is enabled and not triggered via popstate.
		if (!vars.alm_filtering_popstate && !hasMultple) {
			if (typeof window.history.pushState === 'function') {
				const almListing = alm.querySelector('.alm-listing');
				const state = {
					permalink: url,
				};

				// Determine if URL should be updated
				if (almListing && almListing.dataset.filtersUrl !== 'false') {
					// Send Pushstate
					// history.replaceState(state, null, url);
					history.pushState(state, null, url);
					vars.pushstate = true;

					/*
					 * Callback function dispatched after the browser URL has been updated
					 */
					if (typeof window.almFiltersURLUpdate === 'function') {
						window.almFiltersURLUpdate(url);
					}
				}
			}
		}

		vars.alm_filtering_popstate = false;
		data.pause = false; // Disable pause (prevention)
		data.preloaded = false; // Disable preloaded (prevention)
		data.target = id; // Set target before data obj is sent

		dispatch(id, data, url); // Dispatch the data obj and URL.
	});
};

export default triggerChange;
