import analytics from './Analytics';
import setCurrentFilters from './CurrentFilters';
import setQueryDefaults from './QueryDefaults';

/**
 * Send the final output to the almFilter() function in core ALM
 *
 * @since 1.0
 * @param {string} target Target ALM container
 * @param {object} data   Data obj for the filter
 * @param {string} url    The current URL
 * @return void           Dispatch global almFilter() function call
 */
const dispatch = (target, data, url) => {
	// Get the target .ajax-load-more element.
	let alm = document.querySelectorAll('.ajax-load-more-wrap[data-id="' + target + '"]');
	if (!alm) {
		return;
	}
	alm = alm[0];
	const listing = alm.querySelector('.alm-listing.alm-ajax');
	const transition = listing.dataset.transition === null ? 'fade' : listing.dataset.transition;
	const speed = listing.dataset.speed === null ? 200 : listing.dataset.speed;

	// Get default shortcode parameters from localized vars.
	const localized = alm.dataset.localized;
	const defaults = localized && window[localized] && window[localized].defaults ? window[localized].defaults : {};
	data = setQueryDefaults(data, defaults); // Merge query defaults with data.

	// Trigger analytics.
	analytics('filters');

	// Debug Info
	if (listing.dataset.filtersDebug === 'true') {
		console.log('ALM Filters Debug:', data); // eslint-disable-line no-console
	}

	// Dispatch filters to core ALM
	if (typeof ajaxloadmore.filter === 'function') {
		ajaxloadmore.filter(transition, speed, data);
	}

	// Set currently selected filters
	setCurrentFilters(url);
};

export default dispatch;
