import { updateCheckboxLimits } from '../global/UI';
import vars from '../global/Variables';

/**
 * Set the facets.
 *
 * @param {object} facets The current facets.
 */
const setFacets = (facets) => {
	if (!facets) {
		return;
	}

	// Get all filters.
	const filters = vars.alm_filters_facets;
	const target = vars.filter_classname;
	const facetGroups = [];

	if (!filters) {
		return;
	}

	// Loop filters and pull out all potential facets.
	filters &&
		filters.forEach((group) => {
			const filters = group.querySelectorAll(
				`${target}[data-key=taxonomy],
				${target}[data-key=meta],
				${target}[data-key=_author],
				${target}[data-key=_year],
				${target}[data-key=_month],
				${target}[data-key=_day],
				${target}[data-key=category],
				${target}[data-key=category_and],
				${target}[data-key=tag],
				${target}[data-key=tag_and],
				${target}[data-key=postType]`
			);
			group.classList.add('alm-filters-facets-loaded');
			facetGroups.push(Array.from(filters));
		});

	if (!facetGroups) {
		return;
	}

	// Flatten the array and loop results.
	facetGroups.flat().forEach((element) => {
		const key = element.dataset.key; // Get the filter key by dataset.
		const fieldtype = element.dataset.fieldtype; // Get the field type key by dataset.
		const archive = element.dataset.isArchive ? true : false; // Is this an archive page?

		switch (key) {
			case 'taxonomy':
				let slug = element.dataset.taxonomy; // Get the taxonomy slug.
				slug = archive ? slug.replace(/^_/, '') : slug; // Remove leading underscore if archive.

				const taxonomies = facets.taxonomies && facets.taxonomies[slug] ? facets.taxonomies[slug] : '';
				parseTerms(taxonomies, element, fieldtype);
				break;

			case 'category':
				const cats = facets.category ? facets.category : ''; // Get the category slug.
				parseTerms(cats, element, fieldtype);
				break;

			case 'category_and':
				const category_and = facets.category__and ? facets.category__and : '';
				parseTerms(category_and, element, fieldtype);
				break;

			case 'tag':
				const tag = facets.tag ? facets.tag : ''; // Get the tag slug.
				parseTerms(tag, element, fieldtype);
				break;

			case 'tag_and':
				const tag_and = facets.tag__and ? facets.tag__and : ''; //Get the tag__and slug.
				parseTerms(tag_and, element, fieldtype);
				break;

			case 'meta':
				const metaKey = element.dataset.metaKey; // Get the meta key.
				const meta = facets.meta && facets.meta[metaKey] ? facets.meta[metaKey] : '';
				parseTerms(meta, element, fieldtype);
				break;

			case '_author':
				const authors = facets.author ? facets.author : ''; // Get the author slug.
				parseTerms(authors, element, fieldtype);
				break;

			case '_year':
				const year = facets.year ? facets.year : ''; // Get the year slug.
				parseTerms(year, element, fieldtype);
				break;

			case '_month':
				const month = facets.month ? facets.month : ''; // Get the month slug.
				parseTerms(month, element, fieldtype);
				break;

			case '_day':
				const day = facets.day ? facets.day : ''; // Get the day slug.
				parseTerms(day, element, fieldtype);
				break;

			case 'postType':
				const postType = facets.post_type ? facets.post_type : ''; // Get the post_type slug.
				parseTerms(postType, element, fieldtype);
				break;

			default:
				break;
		}
	});
};
export default setFacets;

/**
 * Parse taxonomy and meta filters.
 *
 * @param {object}  facets    Object containing the available facets.
 * @param {Element} element   The facet HTML element.
 * @param {string}  fieldtype The fieldtype.
 */
function parseTerms(facets, element, fieldtype) {
	const facetArray = facets ? Object.keys(facets) : [];
	const hideInactive = element.parentNode.classList.contains('alm-filters-facets-hide-inactive');
	const { hide_inactive_group = '1' } = alm_filters_localize;

	element.dataset.active = facetArray.length; // Set the active filter count.

	// Hide entire filter if contains 0 active items.
	if (facetArray.length === 0 && hideInactive && hide_inactive_group === '1') {
		element.style.display = 'none';
		return;
	}
	element.removeAttribute('style');

	// Switch over field types.
	switch (fieldtype) {
		case 'radio':
		case 'checkbox':
			const items = element.querySelectorAll('li');
			items &&
				items.forEach((item) => {
					const value = item.firstChild.dataset.value;
					const found = facetArray.includes(value);
					const count = facets && facets[value] ? parseInt(facets[value]) : 0;
					const target = item.querySelector('.alm-filter--link');
					target.tabIndex = '0';

					const counter = target.querySelector('.alm-filter-counter');
					if (counter) {
						counter.innerHTML = count;
					}

					// 0 items returned.
					if (count === 0) {
						target.classList.add('disabled');
						target.classList.remove('active');
						target.tabIndex = '-1';
					}

					// `Hide Inactive` option.
					if (hideInactive && fieldtype === 'checkbox') {
						if (found) {
							item.removeAttribute('style');
							enableParentListItems(item);
						} else {
							item.style.display = 'none';
						}
					}

					if (!found && !target.classList.contains('active')) {
						target.classList.add('disabled');
						target.classList.remove('active');
					} else {
						if (count !== 0) {
							target.classList.remove('disabled');
						}
					}
				});

			if (hideInactive) {
				updateCheckboxLimits(element); // Show/Hide checkbox limit buttons.
			}

			break;

		case 'select':
		case 'select_multiple':
			const select = element.querySelector('select');
			const showCount = select.dataset.count === 'true';

			let active = false;
			Array.from(select.options).forEach(function (option) {
				const value = option.value;
				if (value !== '#') {
					const count = facets && facets[value] ? facets[value] : '0';
					const name = option.dataset.name;
					if (showCount) {
						const countTemplate = select.dataset.countTemplate;
						const countDisplay = ` ${countTemplate.replace('%count%', count)}`;
						option.innerHTML = `${name}${countDisplay}`;
					}

					const found = facetArray.includes(value);
					active = found ? true : active;
					option.disabled = found ? false : true;
				}
			});
			// Disable entire select if no found items.
			select.disabled = active ? false : true;

			if (select.disabled) {
				select.classList.add('disabled');
			} else {
				select.classList.remove('disabled');
			}

			break;
	}
}

/**
 * Recursively show parent list items if a child is active.
 *
 * @param {Element} element The target element.
 */
function enableParentListItems(element) {
	if (element.classList.contains('field-child')) {
		// If element is a child, find the parent and enabled it.
		const parentNode = element.parentNode.parentNode;
		parentNode.removeAttribute('style');
		enableParentListItems(parentNode);
	}
}
